import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import {
  Avatar,
  IconButton,
  Badge,
  Dialog,
  DialogTitle,
  DialogContent,
  DialogContentText,
  TextField,
  DialogActions,
  ThemeProvider,
  Button,
  CircularProgress,
  createMuiTheme,
} from '@material-ui/core';
import { Add } from '@material-ui/icons';
import ChatIcon from '@material-ui/icons/Chat';
import { green } from '@material-ui/core/colors';
import DonutLargeIcon from '@material-ui/icons/DonutLarge';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import useStyle from './styles';
import {
  createRoomAction,
  getMyRoomsAction,
} from '../../../actions/roomActions';
import Message from '../../global/Message/Message';
import * as c from '../../../constants/roomConstants';

const theme = createMuiTheme({
  palette: {
    primary: green,
  },
});

const Header = () => {
  const classes = useStyle();
  const dispatch = useDispatch();
  const [open, setOpen] = useState(false);
  const [email, setEmail] = useState('');

  const createRoom = useSelector((s) => s.createRoom);
  const {
    loading: createRoomLoading,
    success: createRoomSuccess,
    err: createRoomErr,
    room: createdRoom,
  } = createRoom;

  const loginUser = useSelector((s) => s.loginUser);
  const { user } = loginUser;

  const handleCloseDialog = () => {
    setOpen(false);
    dispatch({
      type: c.CREATE_ROOM_RESET,
    });
  };

  const handleAddNewChat = (e) => {
    e.preventDefault();
    dispatch(createRoomAction(`${email}`.trim()));
  };

  useEffect(() => {
    if (createRoomSuccess) {
      setEmail('');
      dispatch(getMyRoomsAction());
    }
  }, [createRoomSuccess, dispatch]);
  return (
    <div className={classes.root}>
      <IconButton>
        <Avatar src={`${user.photo}`} />
      </IconButton>
      <div className={classes.sidebarIcons}>
        {/* <IconButton>
          <Badge
            color='secondary'
            variant='dot'
            className={classes.customBadge}
          >
            <DonutLargeIcon />
          </Badge>
        </IconButton> */}
        <Dialog
          open={open}
          onClose={handleCloseDialog}
          //! on close handle dispatch create room reset
          aria-labelledby='form-dialog-title'
        >
          {createRoomLoading ? (
            <Message message='Creating new chat ...' />
          ) : createRoomErr ? (
            <Message severity='error' message={createRoomErr} />
          ) : createRoomSuccess ? (
            <Message severity='success' message='Room created successfully' />
          ) : (
            <DialogTitle id='form-dialog-title'>
              Enter Other Person Email{' '}
            </DialogTitle>
          )}
          <form onSubmit={handleAddNewChat}>
            <DialogContent>
              <ThemeProvider theme={theme}>
                <TextField
                  className={classes.text}
                  autoFocus
                  margin='dense'
                  id='email'
                  label='Email Address'
                  type='email'
                  fullWidth
                  onChange={(e) => setEmail(e.target.value)}
                  value={email}
                  disabled={createRoomLoading}
                />
              </ThemeProvider>
            </DialogContent>
            <DialogActions>
              <Button
                className={classes.addChatDialogBtn}
                onClick={handleCloseDialog}
                variant='contained'
              >
                Cancel
              </Button>
              <Button
                className={classes.addChatDialogBtn}
                disabled={email.length < 1 || createRoomLoading}
                variant='contained'
                type='submit'
                endIcon={
                  createRoomLoading && (
                    <CircularProgress
                      style={{ color: 'black', width: '20px', height: '20px' }}
                    />
                  )
                }
              >
                Add Chat
              </Button>
            </DialogActions>
          </form>
        </Dialog>
        <IconButton onClick={() => setOpen(true)} className={classes.addChat}>
          <Add />
        </IconButton>
        <IconButton>
          <ChatIcon />
        </IconButton>
        <IconButton>
          <MoreVertIcon />
        </IconButton>
      </div>
    </div>
  );
};

export default Header;
