import React, { useState, useEffect, useRef } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import useStyle from './styles';
import Message from './Message/Message';
import { IconButton } from '@material-ui/core';
import KeyboardArrowDownIcon from '@material-ui/icons/KeyboardArrowDown';
import { getRoomsMessagesAction } from '../../../../actions/messageActions';
import { CREATE_MESSAGE_RESET } from '../../../../constants/messagesConstants';

const MessagesContainer = () => {
  const classes = useStyle();
  const dispatch = useDispatch();

  const roomMessages = useSelector((s) => s.roomMessages);
  const {
    loading: loadingMessages = true,
    messages: messagesFromServer,
    err: errGettingMessages,
  } = roomMessages;

  const currentOpenRoom = useSelector((s) => s.currentOpenRoom);
  const { user } = useSelector((s) => s.loginUser);

  const socket = useSelector((s) => s.socket);

  const createMessage = useSelector((s) => s.createMessage);
  const {
    success: sendingMessageSuccess,
    message: sentMessage,
  } = createMessage;

  const [messages, setMessages] = useState([]);

  useEffect(() => {
    dispatch(getRoomsMessagesAction(currentOpenRoom));
  }, [currentOpenRoom, dispatch]);

  //? for displaying messages after loading
  useEffect(() => {
    if (!loadingMessages && !errGettingMessages) {
      if (messagesFromServer.length < 1) {
        setMessages([
          {
            sender: 'admin',
            messageTXT: 'No messages yet in this chat ',
          },
        ]);
      } else {
        setMessages([
          {
            sender: 'admin',
            messageTXT: ' Messages are encrypted',
          },

          ...messagesFromServer,
        ]);
      }
    }
  }, [errGettingMessages, loadingMessages, messagesFromServer, user._id]);

  //? for handling success message sending
  useEffect(() => {
    if (sendingMessageSuccess) {
      setMessages([...messages, sentMessage]);
      dispatch({
        type: CREATE_MESSAGE_RESET,
      });
    }
  }, [dispatch, messages, sendingMessageSuccess, sentMessage]);

  //? for scrolling to end of chat with each render

  useEffect(() => {
    document.getElementById('messages-container').scrollTo(
      0,

      document.getElementById('messages-container').scrollHeight
    );
  });

  //? for listening for new messages
  //! IMPORTANT => this message grabber will only work when user is opening the room
  //! Any room
  //! add new redux reducer for currently displayed messages
  //! and it this component will get its messages from there
  useEffect(() => {}, []);

  useEffect(() => {
    if (socket.on) {
      console.log('Getting new message');
      socket.on('server--user-send-message', (data) => {
        //! IMPORTANT this trick is so important to use setMessages(messages=>[])
        setMessages((messages) => [
          ...messages,
          {
            sender: data.sender,
            messageTXT: data.messageTXT,
            date: new Date(),
          },
        ]);
      });
    }
  }, [socket]);
  return (
    <>
      <div id='messages-container' className={classes.root}>
        {messages.map((msg) => (
          <Message
            key={Math.random()}
            message={msg}
            sender={
              msg.sender === user._id
                ? 'me'
                : msg.sender === 'admin'
                ? 'admin'
                : 'other'
            }
          />
        ))}
      </div>
      <div className={classes.earlierMessagesBtn}>
        <IconButton style={{ width: '40px', height: '40px' }}>
          <KeyboardArrowDownIcon style={{ fontSize: '25px' }} />
        </IconButton>
      </div>
    </>
  );
};

export default MessagesContainer;
