import * as c from '../constants/roomConstants';
import * as urls from '../constants/urls';
import axios from 'axios';

export const getMyRoomsAction = () => async (dispatch, getState) => {
  try {
    dispatch({
      type: c.GET_MY_ROOMS_REQUEST,
    });
    const token = `Bearer ${getState().loginUser.user.token}`;
    const config = {
      headers: {
        authorization: token,
      },
    };

    const {
      data: { data },
    } = await axios.get(`${urls.roomsUrl}/getMyRooms`, config);
    dispatch({
      type: c.GET_MY_ROOMS_SUCCESS,
      payload: data,
    });
  } catch (err) {
    dispatch({
      type: c.GET_MY_ROOMS_FAIL,
      payload:
        err.response && err.response.data.message
          ? err.response.data.message
          : err.message,
    });
  }
};

export const createRoomAction = (otherUserEmail) => async (
  dispatch,
  getState
) => {
  try {
    dispatch({
      type: c.CREATE_ROOM_REQUEST,
    });
    const token = `Bearer ${getState().loginUser.user.token}`;
    const config = {
      headers: {
        'Content-Type': 'application/json',
        authorization: token,
      },
    };

    const {
      data: { data },
    } = await axios.post(
      `${urls.roomsUrl}/createNewRoom`,
      { otherUserEmail },
      config
    );
    dispatch({
      type: c.CREATE_ROOM_SUCCESS,
      payload: data,
    });
  } catch (err) {
    dispatch({
      type: c.CREATE_ROOM_FAIL,
      payload:
        err.response && err.response.data.message
          ? err.response.data.message
          : err.message,
    });
  }
};

export const setCurrentOpenRoomAction = (currentRoomId) => (
  dispatch,
  getState
) => {
  dispatch({
    type: c.SET_CURRENT_OPEN_ROOM,
    payload: currentRoomId,
  });

  localStorage.setItem('currentRoomId', currentRoomId);
};
