import React, { useState, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import useStyles from './styles';
import Sidebar from '../../components/Sidebar/Sidebar';
import ChatBox from '../../components/ChatBox/ChatBox';
import { createSocketAction } from '../../actions/socketActions';

const App = ({ history }) => {
  const classes = useStyles();
  const dispatch = useDispatch();

  const loginUser = useSelector((s) => s.loginUser);
  const { user } = loginUser;

  const socket = useSelector((s) => s.socket);
  const myRooms = useSelector((s) => s.myRooms);
  const { rooms } = myRooms;

  useEffect(() => {
    if (!user || !user.token) {
      history.push('/login');
    } else {
      dispatch(createSocketAction());
    }
  }, [dispatch, history, user]);

  //? set user online
  useEffect(() => {
    const { _id, firstName, lastName, email, photo } = user;
    if (socket.emit) {
      socket.emit('set-me-online', { _id, firstName, lastName, email, photo });
    }
  }, [rooms, socket, user]);

  //? for joining all user rooms

  useEffect(() => {
    if (rooms && socket.emit) {
      const { _id, firstName, lastName, email, photo } = user;
      const roomsToJoin = rooms.map((room) => room._id);
      socket.emit('user-join-his-rooms', {
        rooms: roomsToJoin,
        user: { _id, firstName, lastName, email, photo },
      });
    }
  }, [rooms, socket, user]);

  return (
    <div className={classes.root}>
      <Sidebar />
      <ChatBox />
    </div>
  );
};

export default App;
