import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import { Avatar, IconButton, Badge, ButtonBase } from '@material-ui/core';
import ChatIcon from '@material-ui/icons/Chat';
import Search from '@material-ui/icons/Search';
import MoreVertIcon from '@material-ui/icons/MoreVert';
import useStyle from './styles';

const Header = () => {
  const classes = useStyle();

  const [otherPerson, setOtherPerson] = useState({
    firstName: '',
    lastName: '',
    photo: '',
  });

  const { rooms } = useSelector((s) => s.myRooms);
  const currentOpenRoom = useSelector((s) => s.currentOpenRoom);

  useEffect(() => {
    if (rooms) {
      const roomData = rooms.find((room) => room._id === currentOpenRoom);
      setOtherPerson(roomData.userToShowOnRoom);
    }
  }, [currentOpenRoom, rooms]);
  return (
    <div className={classes.root}>
      <ButtonBase className={classes.userInfoBtnBase}>
        <div className={classes.userInfo}>
          <div className={classes.avatarContainer}>
            <Avatar src={otherPerson.photo} />
          </div>
          <div className={classes.info}>
            <div
              className={classes.name}
            >{`${otherPerson.firstName} ${otherPerson.lastName}`}</div>
            <div className={classes.status}>Online</div>
          </div>
        </div>
      </ButtonBase>

      <div className={classes.sidebarIcons}>
        <IconButton>
          <Search />
        </IconButton>

        <IconButton>
          <MoreVertIcon />
        </IconButton>
      </div>
    </div>
  );
};

export default Header;
