import * as c from '../constants/socketConstants';
import * as urls from '../constants/urls';
import socketIOClient from 'socket.io-client';

export const createSocketAction = () => (dispatch, getState) => {
  const socket = socketIOClient(urls.host);

  dispatch({
    type: c.CREATE_SOCKET,
    payload: socket,
  });
};

//! this action isn't implemented yet
export const disconnectSocketAction = () => (dispatch, getState) => {
  const socket = getState().socket;
  socket.emit('disconnect-me');

  dispatch({
    type: c.DISCONNECT_SOCKET,
  });
};
