import * as c from '../constants/roomConstants';

export const getMyRoomsReducer = (state = {}, action) => {
  switch (action.type) {
    case c.GET_MY_ROOMS_REQUEST:
      return { loading: true };
    case c.GET_MY_ROOMS_SUCCESS:
      return { loading: false, rooms: action.payload };
    case c.GET_MY_ROOMS_FAIL:
      return { loading: false, err: action.payload };
    // case c.UPDATE_LAST_MESSAGE:
    //   return {rooms:{...rooms}}
    default:
      return state;
  }
};

export const createRoomReducer = (state = {}, action) => {
  switch (action.type) {
    case c.CREATE_ROOM_REQUEST:
      return { loading: true };
    case c.CREATE_ROOM_SUCCESS:
      return { loading: false, room: action.payload, success: true };
    case c.CREATE_ROOM_FAIL:
      return { loading: false, err: action.payload };
    case c.CREATE_ROOM_RESET:
      return {};
    default:
      return state;
  }
};

export const currentOpenRoomReducer = (state = '', action) => {
  switch (action.type) {
    case c.SET_CURRENT_OPEN_ROOM:
      return action.payload;
    default:
      return state;
  }
};
