import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { IconButton } from '@material-ui/core';
import { Send } from '@material-ui/icons';
import useStyle from './styles';
import { createMessageAction } from '../../../../actions/messageActions';
import { getMyRoomsAction } from '../../../../actions/roomActions';

const ChatController = () => {
  const classes = useStyle();
  const dispatch = useDispatch();

  const [message, setMessage] = useState('');
  const [typing, setTyping] = useState(false);
  const [messageReceiver, setMessageReceiver] = useState('');

  const { user } = useSelector((s) => s.loginUser);
  const { rooms } = useSelector((s) => s.myRooms);
  const currentOpenRoom = useSelector((s) => s.currentOpenRoom);
  const socket = useSelector((s) => s.socket);

  const handleSubmitMessage = (e) => {
    e.preventDefault();
    if (message) {
      const messageObj = {
        room: currentOpenRoom,
        receiver: messageReceiver,
        messageTXT: message,
        sender: user._id,
      };
      dispatch(createMessageAction(messageObj));
      setTyping(false);
      socket.emit('user-send-message', messageObj);
      setMessage('');
    }
  };

  const handleTypingMessage = (e) => {
    setMessage(e.target.value);
  };

  //? for setting typing variable when typing
  useEffect(() => {
    if (message) {
      setTyping(true);
    } else {
      setTyping(false);
    }
  }, [message]);

  //? for setting message receiver
  useEffect(() => {
    if (rooms) {
      const roomData = rooms.find((room) => room._id === currentOpenRoom);
      setMessageReceiver(roomData.userToShowOnRoom._id);
    }
  }, [currentOpenRoom, rooms]);

  return (
    <form className={classes.root} onSubmit={handleSubmitMessage}>
      <div className={classes.msgBox}>
        <input
          autoFocus={true}
          placeholder='Type a message'
          type='text'
          className={classes.msgInput}
          rows='1'
          value={message}
          onChange={handleTypingMessage}
        ></input>
      </div>
      <div className={classes.send}>
        <IconButton disabled={!message && true} type='submit'>
          <Send style={{ color: '#9B9B9B' }} />
        </IconButton>
      </div>
    </form>
  );
};

export default ChatController;
