import React, { useEffect, useState } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import useStyle from './styles';
import { Avatar, ButtonBase } from '@material-ui/core';
import { setCurrentOpenRoomAction } from '../../../../actions/roomActions';
import moment from 'moment';

const ChatGo = ({ room }) => {
  const classes = useStyle();
  const dispatch = useDispatch();
  const handleSetCurrentRoom = () => {
    dispatch(setCurrentOpenRoomAction(room._id));
  };
  return (
    <ButtonBase onClick={handleSetCurrentRoom} className={classes.btnBase}>
      <div className={classes.root}>
        <div className={classes.avatarContainer}>
          <Avatar src={room.userToShowOnRoom.photo} />
        </div>
        <div className={classes.info}>
          <div className={classes.infoTop}>
            <div
              className={classes.name}
            >{`${room.userToShowOnRoom.firstName} ${room.userToShowOnRoom.lastName}`}</div>
            <div className={classes.date}>
              {moment(room.lastMessage.createdAt).format('hh:mm A')}
            </div>
          </div>

          <div className={classes.lastMsg}>
            {room.new ? (
              <span className={classes.newChat}>New</span>
            ) : (
              <span>{room.lastMessage.messageTXT}</span>
            )}
          </div>
        </div>
      </div>
    </ButtonBase>
  );
};

export default ChatGo;
